.portfolio {
  &-projects {
    background: #fafafa;
    padding: 100px 20px;
    &-list {
      max-width: 1200px;
      margin: 0 auto;
      display: flex;
      @media (max-width: 800px) {
        flex-wrap: wrap;
      }
    }
  }
}
.project {
  display: inline-block;
  vertical-align: top;
  margin: 0;
  padding: 10px;
  @media (max-width: 800px) {
    width: 100%;
  }
  &-link {
    position: relative;
    display: inline-block;
    vertical-align: top;
    font-family: sans-serif;
    overflow: hidden;
    width: 100%;
  }
  &-img {
    width: 100%;
    display: block;
    transition: 1s;
  }
  &-overflow {
    $color: #444;
    position: absolute;
    box-sizing: border-box;
    padding: 10px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    color: #fff;
    justify-content: center;
    align-items: center;
    background: radial-gradient(rgba($color, 0.6), rgba($color, 0.8));
  }
  &-title {
    font-weight: bold;
    font-size: 16px;
  }
  &-text {
    font-size: 13px;
    color: rgba(#fff, 0.8);
  }
  &-title, &-text {
    opacity: 0;
    transform: translateY(1rem);
    transition: 1s;
  }
  &:hover {
    .project-img {
      transform: scale(1.25) rotate(10deg);
    }
    .project-title, .project-text {
      opacity: 1;
      transform: translateY(0);
    }
  }
}